@charset "UTF-8";
*,
:after,
:before {
    box-sizing: border-box
}

body,
html {
    height: 100%
}

body {
    font-family: Arial, Helvetica, sans-serif;
    margin: 0;
    background: #fff;
    color: #000;
    min-height: 100%;
    min-width: 768px;
    position: relative
}

body,
hr {
    padding: 0
}

hr {
    display: block;
    height: 1px;
    border: 0;
    border-top: 1px solid #e5e5e5;
    margin: 59px 5px 44px
}

/* hr.dashed_line {
    background: url(images/templates/hr-dashed.png) 0 0 repeat-x;
    border: none;
    margin: 28px 5px -6px
} */

hr:last-child {
    margin-bottom: 0
}

.line {
    margin: 1px 0 44px
}

/* .common__icons {
    background: url(images/icons/common_icons.png) no-repeat
} */

audio,
canvas,
img,
video {
    vertical-align: middle
}

fieldset {
    border: 0;
    margin: 0;
    padding: 0
}

textarea {
    resize: vertical
}

.hidden {
    display: none!important
}

.float_left {
    float: left
}

.float_right {
    float: right
}

.clear {
    clear: both
}

.left {
    text-align: left!important
}

.center {
    text-align: center!important
}

.right {
    text-align: right!important
}

.full {
    text-align: justify!important
}

.bold {
    font-weight: 700
}

.italic {
    font-style: italic
}

.underline {
    text-decoration: underline
}

.strike,
del {
    text-decoration: line-through
}

.xx-small {
    font-size: 8px!important
}

.x-small {
    font-size: 12px!important
}

.small,
small {
    font-size: 14px!important
}

.large {
    font-size: 18px!important
}

.x-large {
    font-size: 20px!important
}

.xx-large {
    font-size: 22px!important
}

.full_width {
    width: 100%
}

a {
    color: #000;
    transition: color .15s ease-in-out, background-color .15s ease-in-out, border-color .15s ease-in-out
}

a.like_text {
    color: #000;
    cursor: text
}

a.like_text,
a.like_text:hover,
a.pseudo_link {
    text-decoration: none
}

a.pseudo_link {
    border-bottom: 1px dotted #000
}

a:hover {
    text-decoration: none
}

:focus,
a:focus {
    outline: none
}

textarea {
    resize: none
}

.section_title,
h1 {
    font: 24px/28px Arial, Helvetica, sans-serif!important;
    margin: 0 0 17px
}

.user_page_content .section_title,
.user_page_content h1 {
    padding-right: 170px
}

.likeh2,
h2 {
    font: 700 16px/20px Arial, Helvetica, sans-serif;
    margin: 0 0 19px
}

.likeh3,
h3 {
    font: 16px/20px Arial, Helvetica, sans-serif;
    margin: 0 0 19px
}

.likeh4,
h4,
h5 {
    font: 14px/20px Arial, Helvetica, sans-serif;
    margin: 0 0 10px
}

h6 {
    font-size: 16px;
    font-weight: 700;
    margin: 10px 0 8px
}

p {
    font-size: 14px;
    line-height: 18px;
    margin: 0 0 18px
}

p:last-child {
    margin-bottom: 0
}

ul {
    list-style: none;
    margin: 0;
    padding: 0
}

ol {
    list-style-position: outside;
    padding: 0 0 0 20px;
    margin: 0 0 20px
}

li {
    font-size: 14px;
    line-height: 20px
}

.nomarker {
    margin: 0;
    padding: 0
}

.nomargin {
    margin-bottom: 0
}

.marginSide {
    margin: 0 5px!important
}

img.float_left,
img.left {
    float: left;
    margin: 0 20px 20px 0
}

img.float_right,
img.right {
    float: right;
    margin: 6px 0 20px 24px
}

figure.float_left {
    margin: 0 20px 20px 0!important
}

figure.float_right {
    margin: 6px 0 20px 24px!important
}

.video_container.float_left {
    margin: 0 20px 20px 0!important
}

.video_container.float_right {
    margin: 6px 0 20px 24px!important
}

.overflow_hidden {
    overflow: hidden
}

.clearfix,
.grid,
.modal_cart_info {
    *zoom: 1
}

.clearfix:after,
.clearfix:before,
.grid:after,
.grid:before,
.modal_cart_info:before {
    display: table;
    line-height: 0;
    content: ""
}

.clearfix:after,
.grid:after {
    clear: both
}

input,
textarea {
    -webkit-appearance: none;
    border-radius: 0
}

button::-moz-focus-inner {
    padding: 0;
    border: 0
}

::-webkit-input-placeholder {
    color: #ccc
}

:-moz-placeholder,
::-moz-placeholder {
    color: #ccc;
    opacity: 1
}

:-ms-input-placeholder {
    color: #ccc
}

input::-ms-clear {
    display: none
}

.centered_parent {
    display: table;
    width: 100%;
    height: 100%
}

.centered {
    display: table-cell;
    vertical-align: middle
}

.categories_list,
.inline_block_parent,
.pagination_links,
.row_container {
    font-size: 0;
    line-height: 0;
    letter-spacing: -1px
}

.pagination_links {
    float: left
}

.categories_list_item,
.inline_block,
.pagination_item,
.row_container .row_item {
    font-size: 14px;
    line-height: normal;
    letter-spacing: normal;
    display: inline-block;
    vertical-align: top
}

.categories_list_item,
.order_container,
.product_full_gallery_previews img,
.with_border {
    border: 1px solid #ccc
}

.video_container_inner,
.with_boxshadow {
    box-shadow: 0 0 0 1px #b2b2b2
}

.with_marker {
    margin: 0 0 20px
}

.with_marker li {
    position: relative;
    padding-left: 15px;
    line-height: 18px
}

.with_marker li:before {
    content: '';
    display: block;
    /* background: url(images/templates/ul-dot.png) 0 0 no-repeat; */
    width: 4px;
    height: 4px;
    top: 6px;
    left: 0;
    position: absolute
}

.color_grey {
    color: #999
}

.color_grey_dark {
    color: #666
}

.auth {
    display: inline-block;
    margin: 0 0 0 10px;
    float: right;
    color: #fc3;
    text-align: center;
    box-sizing: border-box
}

.auth_enter,
.auth_register {
    color: #fc3;
    text-decoration: none;
    border-bottom: 1px dotted #fc3;
    line-height: 46px
}

.auth_enter:hover,
.auth_register:hover {
    color: #fff;
    border: none
}

.auth_user_link {
    color: #fc3;
    line-height: 46px;
    text-decoration: none
}

.auth_user_link:hover {
    color: #fff;
    border-bottom-color: transparent
}

.banner_container {
    display: block;
    overflow: hidden;
    border: 1px solid #b2b2b2
}

.breadcrumbs {
    clear: both;
    margin: 0 5px 5px;
    font-size: 0;
    line-height: 0;
    letter-spacing: -1px;
    position: relative;
    padding-top: 5px
}

.breadcrumbs li {
    font-size: 16px;
    line-height: normal;
    letter-spacing: normal;
    display: inline-block;
    margin-right: 20px
}

.breadcrumbs IMG {
    margin-left: 10px
}

.products_empty {
    text-align: center;
    margin: 90px 0 25px
}

.products_empty SPAN {
    font-size: 20px
}

.product_description .breadcrumbs {
    margin: 5px 0 0!important
}

.product.product_horizontal .product_description .breadcrumbs {
    display: block;
    padding-top: 10px;
    padding-bottom: 10px
}

.product .product_description .breadcrumbs {
    display: none
}

.product_description .breadcrumbs li {
    font-size: 12px;
    font-weight: 400;
    margin-right: 10px;
    white-space: nowrap
}

.product_description .breadcrumbs li a {
    color: #666;
    text-decoration: underline
}

.product_description .breadcrumbs li a:hover {
    text-decoration: none
}

@media (min-width:0px) and (max-width:999px) {
    .breadcrumbs {
        margin: 0 5px 20px;
        padding-top: 10px
    }
    .product_page .breadcrumbs {
        margin-bottom: 0
    }
    .product_page .breadcrumbs li {
        display: inline-block;
        margin-bottom: 10px
    }
    .search_page .breadcrumbs {
        margin: 24px 5px 23px
    }
    .product_description .breadcrumbs {
        margin: 0!important
    }
}

@media (min-width:1000px) and (max-width:1249px) {
    .product_description .breadcrumbs {
        margin: 0!important
    }
    .cart_page .breadcrumbs {
        margin-bottom: 0
    }
    .product_page .breadcrumbs li {
        display: inline-block;
        margin-bottom: 5px
    }
}

.page_title {
    font-size: 18px;
    color: #999;
    margin: 0 0 -6px 5px
}

.delivery_page .page_title {
    margin-bottom: 14px
}

.page_title_big {
    font-size: 24px;
    color: #000;
    margin: 30px 0 14px 25px;
    font-weight: 700!important
}

.page_title__main .page_title_big {
    margin: 20px 0 14px 5px
}

body.cart_page {
    overflow-x: hidden
}

.cart_page .page_title_big {
    font-weight: 400;
    margin: 0 0 15px 5px
}

.ng-cloak,
[ng-cloak] {
    display: none!important
}

li.cart_row_content {
    list-style-type: none
}

.cart_total_container .ur_content.ur_content_text {
    display: block!important;
    position: absolute;
    margin: 0;
    bottom: 150px;
    display: none;
    font-size: 16px;
    line-height: 25px
}

.cart_row_content:before {
    display: none
}

.cart_row_content .position-index {
    position: absolute;
    top: 13px;
    left: 14px;
    font-weight: 700
}

.tab_content {
    display: block
}

@media (min-width:0px) and (max-width:999px) {
    .page_title_big {
        margin: 20px 0 10px 15px
    }
    .page_title__main .page_title_big {
        margin: 0 0 20px 5px
    }
}

@media (min-width:1000px) and (max-width:1249px) {
    .page_title_big {
        margin: 15px 0 12px 15px
    }
    .page_title__main .page_title_big {
        margin: 15px 0 12px 5px
    }
}

.row_container {
    margin: 0 0 10px -5px
}

.row_container .row_container {
    margin: 0 0 10px -10px
}

.row_container .row_container:last-child {
    margin-bottom: 0
}

.row_container .row_item {
    margin: 0 0 0 10px
}

.row_container .row_item_25 {
    width: 240px
}

.row_container .row_item_50 {
    width: 490px
}

.row_container .row_item_75 {
    width: 740px
}

.row_container .row_item_100 {
    width: 990px
}

