 */

.stepper {
	margin: 0 0 10px 0;
	overflow: hidden;
	position: relative;
}

.stepper .stepper-input {
	border: none;
	color: #333;
	font-size: 13px;
	line-height: 1.2;
	margin: 0;
	overflow: hidden;
	padding: 12px 0 10px;
	width: 40px;
	z-index: 49;
	-moz-appearance: textfield;
}

.stepper .stepper-input::-webkit-inner-spin-button,
.stepper .stepper-input::-webkit-outer-spin-button {
	-webkit-appearance: none;
	margin: 0;
}

.stepper .stepper-input:focus {
	background-color: #fff;
}

.stepper .stepper-arrow {
	background: #fff
		url(../images/templates/jquery.fs.stepper-arrows.png) no-repeat;
	border: 1px solid #ccc;
	cursor: pointer;
	display: block;
	height: 50%;
	position: absolute;
	right: 0;
	text-indent: -99999px;
	width: 25px;
	z-index: 50;
}

.stepper .stepper-arrow.up {
	background-position: center top;
	border-bottom: none;
	border-top: none;
	border-right: none;
	top: 0;
}

.stepper .stepper-arrow.down {
	background-position: center bottom;
	border-bottom: none;
	border-right: none;
	bottom: 0;
}

@media screen and (min-width: 740px) {
	.stepper:hover .stepper-input {
		background-color: #fff;
	}
	.stepper .stepper-step:hover {
		background-color: #f9f9f9;
	}
	.stepper.disabled .stepper-arrow {
		background: #fff;
		border-color: #eee;
		cursor: default;
	}
}

.stepper.disabled .stepper-input {
	background: #fff;
	border-color: #eee;
	color: #ccc;
}

.stepper.disabled .stepper-arrow {
	background: #fff;
	border-color: #eee;
	cursor: default;
}

/**
